.advantages {
  padding-bottom: 60.01px;
  background-color: var(--bcg-btn-hover);
  @media screen and (min-width: 768px) {
  padding-bottom: 204px;
  }
  @media screen and (min-width: 1200px) {
  padding-bottom: 180px;
  }
  }

  .advantages__set {
  width: 230px;
  margin: auto;
  @media screen and (min-width: 768px) {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  }
  @media screen and (min-width: 1200px) {
  
  }
  }
  .advantages__item {
  display: inline-block;
  
    
  @media screen and (min-width: 768px) {
  flex-basis: calc(100% / 3 - 80px);
  margin: auto;
  max-width: 616px;
  
  }
  @media screen and (min-width: 1200px) {
  flex-basis: calc(100% / 3 - 32px);
  margin: auto;
  }
    
  &:not(:last-child) {
  @media screen and (max-width: 767px) {
   margin-bottom: 38px;
  }
  }
    
  &::before {
  display: block;
  content: '';
  margin: auto;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--acc-color);
  margin-bottom: 16px;
  background-size: contain;
  background-repeat: no-repeat;      
  background-position: 50% 50%;
  }
    
  &:nth-child(1)::before {
  background-image: url(../images/advantages/cow.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  background-image: url(../images/advantages/cow@2x.png);
  }
  @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
  background-image: url(../images/advantages/cow@3x.png);
  }
  }
  &:nth-child(2)::before {
  background-image: url(../images/advantages/weight.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
   background-image: url(../images/advantages/weight2x.png);
  }
  @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
  background-image: url(../images/advantages/weight2x.png);
  }
  }
  &:nth-child(3)::before {
  background-image: url(../images/advantages/ice.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  background-image: url(../images/advantages/ice2x.png);
  }
  @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
  background-image: url(../images/advantages/ice3x.png);
  }
  }
  }
  .advantages__number {
    display: block;
    margin-bottom: 20px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 42px;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--card-bcg);
    
    @media screen and (min-width: 768px) {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 59px;
    text-align: center;
    letter-spacing: 0.04em;
    }
    @media screen and (min-width: 1200px){
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 58px;
    line-height: 71px;
    text-align: center;
    letter-spacing: 0.04em;
    }
    }
      
    .advantages__text {
    display: block;
    text-transform: none;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;
    text-align: center;
    color: var(--card-bcg);
    
    @media screen and (min-width: 768px) {
    font-weight: 400;
    font-size: 14px;
    line-height: 174%;
    text-align: center;
    }
    }