.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, 0.2);
    z-index: 100;

}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 568px;
    height: 668px;
    background-color: #FFFFFF;

    border: 2px solid #F8D24E;
    border-radius: 10px;

    padding-top: 32px;
    padding-bottom: 32px;
    padding-left: 55px;
    padding-right: 55px;

}

.modal-close-btn {
    position: absolute;
    top: 20px;
    right: 20px;

    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.modal-close-btn-icon {
    transition: fill 250mc cubic-bezier(0.4, 0, 0.2, 1);
    width: 20px;
    height: 20px;
}

.modal-close-btn:hover .modal-close-btn-icon,
.modal-close-btn:focus .modal-close-btn-icon {
    fill: #0A8817;

}

// ____________modal_form

.modal-title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;

    display: flex;
    align-items: center;
    text-align: center;
    color: #000000;
}

.modal-info {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;

    color: #000000;
    margin-top: 24px;
    margin-bottom: 32px;
}

.modal-form {
    display: flex;
    flex-direction: column;
}

.modal-form-form{
    display: block;
    padding-left: 40px;
    padding-right: 40px;
    margin-bottom: 32px;
}

.modal-form-field-input {
    width: 100%;
    height: 42px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    padding-left: 16px;
    color: #000000;
}

.modal-form-field-input:focus{
    outline: none;
    border: 1.5px solid #0A8817;

}



.modal-form-field-input:focus ~ .modal-form-field-input-desc {
    top: -0.5px;
    color: #0A8817;
    font-size: 10px;
    padding: 0 10px;
    border-left: 1px solid #0A8817;
    border-right: 1px solid #0A8817;

    background: #FFFFFF;

}

.modal-form-field-comment {
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
}


.modal-form-field-input-desc {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);

    text-transform: capitalize;
    pointer-events: none;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    transition: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-field {
    position: relative;
    display: block;
    margin-bottom: 16px;
}

.modal-form-comment {
    position: relative;
    display: block;
}



.modal-form-textarea {
    resize: none;
    width: 100%;
    height: 84px;
    padding: 10px 16px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    padding-left: 16px;
    color: #000000;
    transition: border 250mc cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-field-input-comment {
    position: absolute;
    top: 20%;
    left: 16px;
    transform: translateY(-20%);
    text-transform: capitalize;
    pointer-events: none;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    transition: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-textarea:focus {
    outline: none;
    border: 1.5px solid #0A8817;
}

.modal-form-textarea:focus ~ .modal-form-field-input-comment {
    top: -4px;
    color: #0A8817;
    font-size: 10px;
    padding: 0 10px;
    border-left: 1px solid #0A8817;
    border-right: 1px solid #0A8817;

    background: #FFFFFF;

}




.modal-form-submit {

    align-self: center;
}


